'use client';

// Specification: Form to create a new job application.
// Renders required fields, performs minimal validation, and submits via provided callback.

import { FormEvent, useState } from 'react';
import { CreateJobApplicationPayload } from '../../services/jobApplicationsApi';
import { JobStatus } from '../../types/jobApplication';
import { SuggestionInput } from './SuggestionInput';

const formatStatusLabel = (status: JobStatus) => status.charAt(0).toUpperCase() + status.slice(1);

type FormularioPostulacionProps = {
  onSubmit: (payload: CreateJobApplicationPayload) => Promise<void>;
  loading?: boolean;
  error?: string | null;
  success?: string | null;
  suggestions?: {
    positions: string[];
    sources: string[];
  };
};

const today = () => new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
  .toISOString()
  .slice(0, 10);

const initialState = (): CreateJobApplicationPayload => ({
  company: '',
  position: '',
  source: '',
  applicationDate: today(),
  status: JobStatus.ENVIADA,
  notes: '',
  jobUrl: '',
});

export function FormularioPostulacion({
  onSubmit,
  loading = false,
  error = null,
  success = null,
  suggestions = { positions: [], sources: [] },
}: FormularioPostulacionProps) {
  const [form, setForm] = useState<CreateJobApplicationPayload>(initialState());
  const [localError, setLocalError] = useState<string | null>(null);

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    setLocalError(null);

    if (!form.company || !form.position || !form.source || !form.applicationDate || !form.status) {
      setLocalError('Completa los campos obligatorios');
      return;
    }

    try {
      await onSubmit({
        ...form,
        notes: form.notes || undefined,
        jobUrl: form.jobUrl || undefined,
      });
      setForm(initialState());
    } catch (err) {
      const message = err instanceof Error ? err.message : 'Error al crear postulacion';
      setLocalError(message);
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLFormElement>) => {
    if ((e.ctrlKey || e.metaKey) && e.key === 'Enter') {
      handleSubmit(e as unknown as FormEvent);
    }
  };

  const handleChange = (field: keyof CreateJobApplicationPayload) => (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>,
  ) => {
    setForm((prev) => ({ ...prev, [field]: e.target.value }));
  };

  const handleValueChange = (field: keyof CreateJobApplicationPayload) => (value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  return (
    <form
      onSubmit={handleSubmit}
      onKeyDown={handleKeyDown}
      className="space-y-4 rounded-xl border border-slate-200 bg-white p-4 shadow-sm"
    >
      <div className="space-y-1">
        <h2 className="text-lg font-semibold text-slate-900">Registrar nueva postulación</h2>
        <p className="text-sm text-slate-600">Sumá tu siguiente paso y mantené visible tu progreso.</p>
      </div>

      <div className="grid gap-4 md:grid-cols-2">
        <SuggestionInput
          label="Puesto"
          value={form.position}
          onChange={handleValueChange('position')}
          options={suggestions.positions}
          required
        />
        <div className="space-y-1">
          <label className="text-sm font-medium text-slate-700">Empresa *</label>
          <input
            type="text"
            className="w-full rounded-lg border border-slate-200 px-3 py-2 text-sm focus:border-slate-400 focus:outline-none"
            value={form.company}
            onChange={handleChange('company')}
            required
          />
        </div>
        <SuggestionInput
          label="Fuente"
          value={form.source}
          onChange={handleValueChange('source')}
          options={suggestions.sources}
          required
        />
        <div className="space-y-1">
          <label className="text-sm font-medium text-slate-700">Fecha de postulación *</label>
          <div className="relative">
            <input
              type="date"
              className="w-full appearance-none rounded-lg border border-slate-200 px-3 py-2 pr-10 text-sm focus:border-slate-400 focus:outline-none [&::-webkit-calendar-picker-indicator]:h-full [&::-webkit-calendar-picker-indicator]:w-full [&::-webkit-calendar-picker-indicator]:appearance-none [&::-webkit-calendar-picker-indicator]:cursor-pointer [&::-webkit-calendar-picker-indicator]:opacity-0"
              value={form.applicationDate}
              onChange={handleChange('applicationDate')}
              required
            />
            <span className="pointer-events-none absolute inset-y-0 right-3 flex items-center text-slate-500">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-4 w-4"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
                aria-hidden="true"
              >
                <path d="M6 9l6 6 6-6" />
              </svg>
            </span>
          </div>
        </div>
        <div className="space-y-1">
  <label className="text-sm font-medium text-slate-700">Estado *</label>

  <div className="relative">
    <select
      className="w-full appearance-none rounded-lg border border-slate-200 px-3 py-2 pr-10 text-sm
                 focus:border-slate-400 focus:outline-none"
      value={form.status}
      onChange={handleChange("status")}
      required
    >
      {Object.values(JobStatus).map((status) => (
        <option key={status} value={status}>
          {formatStatusLabel(status)}
        </option>
      ))}
    </select>

    {/* Flecha custom */}
    <span className="pointer-events-none absolute inset-y-0 right-3 flex items-center text-slate-500">
      ▼
    </span>
  </div>
</div>

        <div className="space-y-1">
          <label className="text-sm font-medium text-slate-700">URL de la oferta</label>
          <input
            type="url"
            className="w-full rounded-lg border border-slate-200 px-3 py-2 text-sm focus:border-slate-400 focus:outline-none"
            value={form.jobUrl}
            onChange={handleChange('jobUrl')}
            placeholder="https://..."
          />
        </div>
      </div>

      <div className="space-y-1">
        <label className="text-sm font-medium text-slate-700">Notas</label>
        <textarea
          className="w-full rounded-lg border border-slate-200 px-3 py-2 text-sm focus:border-slate-400 focus:outline-none"
          value={form.notes}
          onChange={handleChange('notes')}
          rows={3}
          placeholder="Detalles, próximos pasos o recordatorios"
        />
      </div>

      {(localError || error) && (
        <div className="rounded-lg border border-red-200 bg-red-50 px-3 py-2 text-sm text-red-700">
          {localError || error}
        </div>
      )}
      {success && !localError && !error && (
        <div className="rounded-lg border border-green-200 bg-green-50 px-3 py-2 text-sm text-green-700">
          {success}
        </div>
      )}

      <div className="flex justify-end">
        <button
          type="submit"
          className="rounded-lg bg-slate-900 px-4 py-2 text-sm font-semibold text-white transition hover:bg-slate-800 disabled:cursor-not-allowed disabled:bg-slate-400"
          disabled={loading}
        >
          {loading ? 'Guardando...' : 'Registrar postulación'}
        </button>
      </div>
    </form>
  );
}
